/* -------------------- Logo Start -------------------- */
.logo{
    padding: 20px;
    display: flex;
    height: 20vh;
    align-items: center;
    gap: 12px;
  }
  .logo img{
    width: 6.5vw;
    height: auto;
  }
  .logo h1{
    font-size: 28px;
    color: #FAF9F6;
  }
  /* -------------------- Logo Start -------------------- */
  @media (max-width: 768px) {
    .logo{
      height: 8vh;
      justify-content: center;
    }
    .logo img{
      width: 3em;
    }
    .logo h1{
      font-size: 25px;
    }
  }