/* -------------------- Screen Start -------------------- */
.main{
    display: flex;
    justify-content: center;
    /* background-color: #DCF8C6; */
    width: 100%;
    height: 80vh;
}
.scr{
    display: flex;
    flex-direction: row;
    gap: 0.5vw;
    background-color: #25D366;
    width: 60%;
    border-radius: 12px;
    height: 75vh;
    border-style: solid;
    border-color: #25D366;
    border-width: 4px;
}
.msg-box{
    display: flex;
    flex-direction: column;
    border-radius: 12px;
    background-color: #075E54;
    width: 50%;
    height: 100%;
}
.heading{
    display: flex;
    align-items: center;
    background-color: #252525;
    width: 100%;
    height: 12%;
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;
}
.textview{
    background-color: transparent;
    width: 100%;
    height: 72%;
}
.textboxview{
    margin-top: auto;
    margin-bottom: 2%;
    display: flex;
    gap: 4px;
    align-items: center;
    width: 100%;
    height: 15%;
}

.textboxview .textbox{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-left: 1%;
    width: 82%;
    height: 3.2em;
    background-color: #252525;
    border-radius: 50px;
}
.textbox .emoji{
    margin-left: 4.5%;
    width: 8%;
    height: 50%;
    cursor: pointer;
}
.textbox .txt{
    color: white;
    font-family:"helvetica";
    margin: 2%;
    padding-left: 4px;
    width: 70%;
    height: 60%;
    font-size: 125%;
    background-color: transparent;
    border-color: transparent;
    border: none;
    resize: none;
    overflow: visible;
    line-height: 1.5;
    text-wrap: wrap;
    caret-color: #25D366;
    scrollbar-width: thin;
    scrollbar-color: #888 transparent;
}
.textbox .txt::-webkit-scrollbar-thumb {
    background-color: rgba(0, 0, 0, 0.2); /* Color of the scroll thumb */
    border-radius: 10px; /* Rounded corners of the thumb */
    border: 3px solid transparent; /* Border around the thumb */
  }

.textbox .txt:focus {
    border: none; /* Remove border when focused */
    outline: none; /* Remove outline when focused */
}

.textbox .pin{
    margin-left: auto;
    margin-right: 4.5%;
    width: 8%;
    height: 50%;
    cursor: pointer;
}
.textbox .camara{
    margin-right: 4.5%;
    width: 8%;
    height: 50%;
    cursor: pointer;
}

.textboxview .voicemsg{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 14%;
    height: 3.2em;
    border-radius: 50px;
    background-color: #25D366;
}
.voicemsg .voice{
    width: 70%;
    cursor: pointer;
}
.voicemsg .send{
    width: 38%;
    cursor: pointer;
}

.msg-box .heading img{
    width: 2.2em;
    margin-left: 15px;
    border-radius: 50px;
}
.msg-box .heading .user{
    display: flex;
    flex-direction: column;
    gap: 4px;
}
.msg-box .heading .user .author{
    margin-left: 12px;
    font-size: 1.2em;
    color: white;
    font-family: 'Trebuchet MS', sans-serif;
}
.msg-box .heading .user .status{
    margin-left: 12px;
    font-size: 0.8em;
    color: white;
    font-family: 'Trebuchet MS', sans-serif;
}

.msg-box .heading .icon{
    width: 2.2em;
    margin-left: 15px;
    border-radius: 50px;
    cursor: pointer;
}
.msg-box .heading .ellipsis{
    width: 1.5em;
    margin-right: 10px;
    border-radius: 50px;
    cursor: pointer;
}
.msg-box .heading .call{
    width: 1.3em;
    margin-left: 18px;
    border-radius: 50px;
    cursor: pointer;
}
.msg-box .heading .vcall{
    width: 1.6em;
    margin-left: auto;
    margin-right: 5px;
    border-radius: 50px;
    cursor: pointer;
}
button{
    display: none;
}
.des{
    display: block;
}
/* -------------------- Screen End -------------------- */

@media (max-width: 768px) {
    .main{
        height: 90vh;
    }
    .scr{
        flex-direction: column;
        width: 90%;
        height: 85vh;
    }
    .msg-box{
        border-radius: 12px;
        background-color: #075E54;
        width: 100%;
        height: 93%;
    }
    .heading{
        height: 8%;
        border-top-left-radius: 12px;
        border-top-right-radius: 12px;
    }
    .textview{
        background-color: transparent;
        width: 100%;
        height: 83%;
    } 
    .textboxview{
        margin-bottom: 2%;
        height: 7.9%;
    }
    button{
        display: block;
        height: 6%;
        margin-top: 1%;
        border-radius: 12px;
        background-color: #075E54;
        color: white;
        font-family: 'Trebuchet MS', sans-serif;
        font-size: 1.8em;
        border-color: transparent;
    } 
    .hidden{
        display: none;
    }
}