::-webkit-scrollbar {
    width: 8px; /* Width of the entire scrollbar */
  }
  
  ::-webkit-scrollbar-track {
    background: transparent; /* Color of the tracking area */
  }
  
  ::-webkit-scrollbar-thumb {
    background-color: rgba(0, 0, 0, 0.2); /* Color of the scroll thumb */
    border-radius: 10px; /* Rounded corners of the thumb */
    border: 3px solid transparent; /* Border around the thumb */
  }

.chat-container {
    height: 100%;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    gap: 1.5%;
  }
  .default{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 10px;
  }
  .default p{
    margin-top: 10px;
    padding: 7px;
    border-radius: 20%;
    background-color: #36353A;
    color: rgb(228, 228, 228);
    font-size: 0.8em;
  }
/* sender bubble */
.sender {
    display: inline-block;
    position: relative;
	width: auto;
    max-width: 70%;
	height: auto;
    margin-left: auto;
    margin-right: 4%;
}
.tri-right.left-top:after{
	content: ' ';
	position: absolute;
	width: 0;
	height: 0;
	right: -10px;
    top: 0px;
	bottom: auto;
	border: 12px solid;
	border-color: #328b53 transparent transparent transparent;
}
.sender .talktext{
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 3px 6px;
    text-align: left;
    line-height: 1.2em;
    color: white;
    background-color: #328b53;
    border: 2px solid transparent;
    border-radius: 10px;
    font-size: 1em;
    font-family: 'Trebuchet MS', sans-serif;
    position: relative;
    z-index: 1;
}
.sender .talktext p{
    display: flex;
    flex-direction: row;
    gap: 0px;
}
.sender .talktext p span{
    margin-left: 10px;
    margin-top: auto;
    margin-bottom: -5px;
    color: rgb(228, 228, 228);
    font-size: 0.65em;
    font-family: 'Trebuchet MS', sans-serif;
}

.receiver {
    display: inline-block;
    position: relative;
	width: auto;
    max-width: 70%;
	height: auto;
    margin-left: 4%;
    margin-right: auto;
}
.tri-left.right-top:after{
